/* RESET */

body {
  margin: 0;
  padding: 0;
  font-family: Helvetica;

  background-color: #efefef;
}

/* FLEX */

.flex {
  display: -ms-flexbox;
  display: flex;
}

.vertical {
  -ms-flex-direction: column;
      flex-direction: column;
}

@media screen and (max-width: 800px){
  .horizontal {
    -ms-flex-direction: column;
        flex-direction: column;
  }
}

.flex-center {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.flex-1 {
  -ms-flex: 1 1;
      flex: 1 1;
}

.flex-2 {
  -ms-flex: 2 1;
      flex: 2 1;
}

.flex-3 {
  -ms-flex: 3 1;
      flex: 3 1;
}

/* STRUCTURE */

.container {
  margin: 5px;
  padding: 5px;
}

/* INPUTS */

.textarea-code {
  width: calc(100% - 5px);
  resize: none;
}

/* COMPONENTS */

.Menu {
  background-color: #fefefe;
  padding: 20px 15px;
}

.Menu > .title {
  display: inline;
}

.Menu > .right {
  float: right;
}

.Footer {
  text-align: center;
}

.Generator {
  padding: 30px 0;
  width: 100%;
}

.Display {
  margin: 15px 0;
  width: 100%;
}


